import React from 'react'
import Layout from '../components/layout/Layout'
import { useAuth } from '../context/authHook'

const HomePage = () => {
const [auth,setAuth]=useAuth()

  return (
   
    <Layout>
    <h1>homepage</h1>
    <pre>{JSON.stringify(auth,null,6)}</pre>
    </Layout>
    
  )
}

export default HomePage