import React from 'react'
import Layout from '../../components/layout/Layout'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import  toast  from 'react-hot-toast'
import axios from 'axios'
import '../../styles/AuthStyle.css'
import { useAuth } from '../../context/authHook'

const Login = () => {

  
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [auth,setAuth]= useAuth()

    const navigate=useNavigate()

    // <<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<<COPYING FUNCTION

    const handleSubmit = async(e)=>{
        e.preventDefault();
        const port = "http://localhost:8080"
    //    toast.success('login')
    //>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>> try api req handle
     try{
        const res = await axios.post(`${port}/api/v1/auth/login`,
        {

            email,
            password,
        })
        if(res && res.data.success){
            toast.success(res.data.message)
            setAuth({
                ...auth,
                user:res.data.user,
                token:res.data.token
            })
            localStorage.setItem('auth',JSON.stringify(res.data));
            setTimeout(()=>{
                navigate('/home')
            },3000)
            
        }else{
            toast.error(res.data.message)
        }
    
     }
     catch(error){
        console.log(error)
        toast.error("Email doesn't exist")
     }
    
    }


  return (
   <Layout>
      <div className="form-container">
                <h1>Login Now</h1>
                <form onSubmit={handleSubmit}>
                   
                    <div className="mb-3">

                        <input type="email" className="form-control" value={email} name='email'
                            id="exampleInputemail" placeholder='example@gmail.com'
                            onChange={ (e)=>{setEmail(e.target.value)}} required />

                    </div>
                    <div className="mb-3">

                        <input type="password" className="form-control" value={password} name="password"
                            id="exampleInputpassword" placeholder='********'
                            onChange={ (e)=>{setPassword(e.target.value)}} required />

                    </div>
                  
             

                    <button type="submit" className="btn btn-primary">LOGIN</button>
                </form>

            </div>
   </Layout>
  )
}

export default Login; 