import React from 'react'
import { NavLink,Link, useNavigate } from 'react-router-dom'
import {HiOutlineShoppingCart} from 'react-icons/hi'
import { useAuth } from '../../context/authHook'
import { toast } from 'react-hot-toast'

const Header = () => {
const [auth,setAuth]=useAuth()

// hadle>>>>>>>>>>>>>>>>>>>>>>>>>>>> LOGOUT
const navigate = useNavigate()
const handleLogout=()=>{
  setAuth({
    ...auth,
    user:null,
    token:'',
  
  })
  localStorage.removeItem('auth')
  toast.success("Logout successfully")
  setTimeout(()=>{
    navigate('/login')
  },3000)
  
  
}



  return (
    <>
    <nav className="navbar navbar-expand-lg bg-body-tertiary">
  <div className="container-fluid">
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarTogglerDemo01" aria-controls="navbarTogglerDemo01" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon"></span>
    </button>
    <div className="collapse navbar-collapse" id="navbarTogglerDemo01">
      <Link className="navbar-brand" to="/"><HiOutlineShoppingCart/> Hidden brand</Link>
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className="nav-item">
          <NavLink className="nav-link" aria-current="page" to='/'>Home</NavLink>
        </li>
        <li className="nav-item">
          <NavLink className="nav-link " to='/category'>Category</NavLink>
        </li>
        {/* >>>>>>>>>>>>>>>>> Conditioning  */}
          {
            !auth.user ?
// >>>>>>>>>>>>>>>>>>>>>>>>>>>>>IF<>><>><<><><>><><><><><><>,
           ( <>
             <li className="nav-item">
          <NavLink to="/register" className="nav-link" >Sign Up</NavLink>
        </li>
        <li className="nav-item">
          <NavLink to="/login" className="nav-link" >LogIn</NavLink>
          {/* light has gone */}
        </li>
            
            </>)
             :
        // >>>>>>>>>>>>>>>>>>>>>>>> ELSE<><<><>,,,<><><><>>>><.

        (    <>

<li className="nav-item dropdown">
          <Link className="nav-link dropdown-toggle" to="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            {auth?.user.name}
          </Link>
          <ul className="dropdown-menu">         
                                                    {/* >>>>>>>><><<>><>, CONDITION CHECKING FOR ADMIN USER */}
            <li><NavLink className="dropdown-item" to={`/dashboard/${auth?.user?.role === 1 ? "admin": "user"}`}>Dashboard</NavLink></li>
            <li className="nav-item">
          <NavLink to="/login" className="dropdown-item" onClick={handleLogout} >Logout</NavLink>
          {/* light has gone */}
        </li>
          </ul>
        </li>
            
       
            </>)
          }
        <li className="nav-item">
          <NavLink to="/cart" className="nav-link" >Cart{0}</NavLink>
          {/* light has gone */}
        </li>
    
    
       
       
      </ul>
     
    </div>
  </div>
</nav>
    </>
  )
}

export default Header