import React from 'react'
import Layout from '../../components/layout/Layout'
import { useState } from 'react'
import { useNavigate } from 'react-router-dom'
import  toast  from 'react-hot-toast'
import axios from 'axios'
import '../../styles/AuthStyle.css'



const Register = () => {
    // >>>>>>>>>>>>>>>>>>>>>>>>>>>>CREATING STATE
    const [name, setName] = useState("")
    const [email, setEmail] = useState("")
    const [password, setPassword] = useState("")
    const [phone, setPhone] = useState("")
   
    const navigate=useNavigate()

const handleSubmit = async(e)=>{
    e.preventDefault();
    const port = "http://localhost:8080"
//    toast.success('register sucessfully')
//>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>>> try api req handle
 try{
    const res = await axios.post(`${port}/api/v1/auth/register`,
    {
        name,
        email,
        password,
        phone,
        // user
        

    })
    if(res && res.data.success){
        toast.success(res.data.message)
        setTimeout(()=>{
            navigate('/login')
        },3000)
        
    }else{
        toast.error(res.data.message)
    }

 }
 catch(error){
    console.log(error)
    toast.error("Something Went Wrong")
 }



}





    return (
        < Layout>
            <div className="form-container">
                <h1>Register Now</h1>
                <form onSubmit={handleSubmit}>
                    <div className="mb-3">

                        <input type="text" className="form-control" value={name} name='name'
                            id="exampleInputName" placeholder='Enter your name'
                            onChange={(e)=>{setName(e.target.value)} } required />

                    </div>
                    <div className="mb-3">

                        <input type="email" className="form-control" value={email} name='email'
                            id="exampleInputemail" placeholder='example@gmail.com'
                            onChange={ (e)=>{setEmail(e.target.value)}} required />

                    </div>
                    <div className="mb-3">

                        <input type="password" className="form-control" value={password} name="password"
                            id="exampleInputpassword" placeholder='********'
                            onChange={ (e)=>{setPassword(e.target.value)}} required />

                    </div>
                    <div className="mb-3">

                        <input type="number" className="form-control" value={phone} name= 'phone'
                            id="exampleInputphone" placeholder='03333333333'
                            onChange={(e)=>{setPhone(e.target.value)} } required />

                    </div>


        {/* for radio button */}
                    {/* <div className="mb-3">

                        <input type="radio" className="form-control" value={phone} name= 'phone'
                            id="exampleInputphone" placeholder='03333333333'
                            onChange={(e)=>{setPhone(e.target.value)} } required />

                    </div> */}
                    {/* <div className="mb-3">

                        <input type="address" className="form-control" value={address} name='address'
                            id="exampleInputaddress" placeholder='Address'
                            onChange={ (e)=>{setAddress(e.target.value)}} required />
                    </div> */}

                    <button type="submit" className="btn btn-primary">REGISTER</button>
                </form>

            </div>
        </Layout>
    )
}

export default Register



